import { Box } from "@mui/material"
import { useEffect } from "react";
import bgImg from "../assets/h55.png";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const ProductInfoWithZoom = () => {
	useEffect(() => {

		gsap.registerEffect({
			name: "zoom",
			effect: (targets, config) => {
				console.log("tagets", targets);
				console.log("config", config);
				const vars = { transformOrigin: "0px 0px", ...config },
					{ scale, origin } = config,
					//  clamp(minimum, maximum, valueToClamp)
					// pass 0 ,100 , 105 - it's return 100
					clamp = gsap.utils.clamp(-100 * (scale - 1), 0);
				console.log("clamp", clamp(-100 * (scale - 1), 0));
				delete vars.origin;
				vars.xPercent = clamp((0.5 - origin[0] * scale) * 100);
				console.log("clamp2", clamp((0.5 - origin[0] * scale) * 100));

				vars.yPercent = clamp((0.5 - origin[1] * scale) * 100);
				vars.overwrite = "auto";
				return gsap.to(targets, vars);
			},
			extendTimeline: true,
			defaults: { origin: [0.5, 0.5], scale: 2 }
		});


		const zoomData = [
			{ scale: 1, origin: [0.5, 0.5] },
			{ scale: 1.5, origin: [0.5, 0.5] },
			{ scale: 2, origin: [0.8, 0.5] },
			{ scale: 1.5, origin: [0.2, 0.5] }
		];

		gsap.utils.toArray("section").forEach((section, index) => {
			const zoom = zoomData[index];
			ScrollTrigger.create({
				trigger: section,
				start: "top 45%",
				end: "+=125%",
				onToggle(self) {
					if (self.isActive) {
						gsap.effects.zoom(".photo", {
							scale: zoom.scale,
							origin: zoom.origin,
							duration: 1,
							ease: "power1.inOut"
						});
					}
				}
			});
		});
	}, [])
	return (
		<Box sx={{
			px: 10,
			"& .right-side": {
				width: "40vw",
				position: "fixed",
				right: 80,
				top: "50%",
				transform: "translateY(-50%)",
				height: "40vw",
				overflow: "hidden",
			},
			"& section": {
				position: "relative",
				height: "100vh",
				width: "calc(60vw - 100px)",
				display: "flex",
				flexDirection: "column",
				justifyContent: "center",
				"& .EachSectionInfo": {
					maxWidth: 700,
				},
				"& h1": {
					fontSize: 40,
					letterSpacing: 1,
					textTransform: "uppercase",
				},
				"& p": {
					fontWeight: 500,
					marginTop: "2rem",
					letterSpacing: 0.5,
					fontSize: 24,
					lineHeight: 1.6,
				}
			},
			"& .photo": {
				width: "100%",
				height: "100%",
				backgroundImage: `url(${bgImg})`,
				backgroundSize: "contain",
				backgroundRepeat: "no-repeat",
				backgroundPosition: "center",
			}
		}}>
			<Box className="right-side">
				<Box className="photo"></Box>
			</Box>
			<Box component="section">
				<Box className="EachSectionInfo">
					<h1>Immersive music</h1>
					<p>
						Experience immersive music with deep bass using these headphones. With powerful 40 mm sound drivers, they provide exceptional bass and precise high frequencies, allowing you to hear every detail of your favorite songs. The audio experience is truly dramatic and captivating.
					</p>
				</Box>
			</Box>
			<Box component="section">
				<Box className="EachSectionInfo">
					<h1>Travel-Friendly Design</h1>
					<p>These headphones are designed to be portable and travel-friendly. With their foldable design, they can easily be stored and carried with you wherever you go, whether it's a trip to the gym, a vacation, or just lounging at home.</p>
				</Box>
			</Box>
			<Box component="section">
				<Box className="EachSectionInfo">
					<h1>HIGH QUALITY SOUND</h1>
					<p>
						Enjoy a seamless transition between wired and wireless modes with these headphones, enhancing your listening experience. You can effortlessly switch between the two modes, connecting to your devices in whichever way
						suits you best. Benefit from the flexibility of wireless communication combined with the reliability of corded audio.
					</p>
				</Box>
			</Box>
			<Box component="section">
				<Box className="EachSectionInfo">
					<h1>EXCEPTIONAL COMFORT</h1>
					<p>These headphones are designed with your comfort in mind, featuring velvety and super-soft cushions that cradle your ears. Enjoy long listening sessions without discomfort or weariness, and listen to your favourite music,
						podcasts, and phone conversations in total comfort.</p>
				</Box>
			</Box>

		</Box >
	)
}

export default ProductInfoWithZoom;